import paintDataManager from "../paintDataManager";
import galleryitem from "./galleryitem";
import { tianSeManager } from "../tianSeManager";
import { paintAssetsManager } from "../paintAssetsManager";
import TigoManager from "../TigoManager";
import { storageParams } from "../paintInterface";
import EventSystem from "../../../../kit/system/event/EventSystem";

const { ccclass, property } = cc._decorator;
@ccclass
export default class Gallery extends cc.Component {

    @property(cc.Prefab)
    public itemPrefab: cc.Prefab = null;

    @property(cc.Prefab)
    public showItemPrefab: cc.Prefab = null;

    @property(cc.Prefab)
    public galleryItemPrefab: cc.Prefab = null;

    @property(cc.Node)
    public listNode: cc.Node = null;

    @property(cc.ScrollView)
    public scrollView: cc.ScrollView = null;

    frame_arr = []
    select_Index = -1
    paintItemComponentMap = new Map();

    public onLoad() {
        console.log(" ===")
        EventSystem.on("callCocos_data",this.callCocos_data,this);
    }

     public async callCocos_data(_data){
        console.log("tianSeNode==========",_data)
        let data = _data.data
        if(data.action == "TiGoStorageSuccess"){
            if(data.param.unique == "gallery_all")
            {   
                let saveData = paintDataManager.getSavePaintData()
                await Promise.all(saveData.map((element)=>{
                    return new Promise<void>((resolve, reject) => {
                        let base64 = data.param.data["paint_" +element.propId]
                        if(base64)
                        {   
                            console.log("Base64 paint_" +element.propId)
                            let img = new Image();
                            img.src = base64;
                            // img.onload = () =>{
                                let texture = new cc.Texture2D();
                                texture.initWithElement(img);
                                paintAssetsManager.instance.setPaintTextureById(element.propId,texture)
                                resolve()
                            // }
                        }else{
                            resolve()
                        }
                    });
                }))
                this.initList()
            }
        }
    }

    public start() {
        if(tianSeManager.instance.open_gallery_in_firstPaint)
        {
            this.initList()
        }else{
            tianSeManager.instance.m_pOwner.setShowLoading(true)
            let platform = paintDataManager.getPlatform()
            if(platform == "cn"){
                this.loadPaintFrame()  
            }else{
                let params:storageParams= {
                    method:"GET",
                    key:"",
                    getAll:"1",
                    type:"file",
                    unique:"gallery_all",
                }
                console.log("Base64===========")
                TigoManager.instance.callAppToStorage(params)
            }
        }
    }

    private assetMap = [];
    async loadPaintFrame(){
        let data = paintDataManager.getSavePaintData()
        await Promise.all(data.map((element,i)=>{
            return new Promise<void>((resolve, reject) => {
                if(cc.sys.isNative){
                    let path = paintDataManager.getNativePathByIndex(element.propId)
                    let filePath = jsb.fileUtils.getWritablePath() + path;
                    if (!jsb.fileUtils.isFileExist(filePath)) {
                        console.log(`${filePath} `);
                        resolve();
                        return;
                    }
                    cc.assetManager.loadRemote(filePath,(err,res:any)=>{
                        if(err)
                        {
                            console.log("",err)
                        }
                        this.assetMap.push(res);
                        console.log(""+element.native_path)
                        paintAssetsManager.instance.setPaintTextureById(element.propId,res)
                        resolve()
                    })
                }else{
                    resolve()
                }
            });
        }));
        this.initList()
    }

    /*  */
    public initList() {
        console.log("")
        tianSeManager.instance.m_pOwner.setShowLoading(false)
        let data = paintDataManager.getPaintListData()
        let len = data.length
        for (let i = 0; i < len; i+=4) {
            let galleryNode =  cc.instantiate(this.itemPrefab);
            this.listNode.addChild(galleryNode)
            for (let j = 0; j < 4; j++) {
                let paintData = data[i+j]
                if(paintData)
                {
                    let galleryItem: cc.Node = cc.instantiate(this.galleryItemPrefab);
                    let galleryitemComponent: galleryitem = galleryItem.getComponent<galleryitem>(galleryitem);
                    galleryitemComponent.init(paintData);
                    this.paintItemComponentMap.set(paintData.propId,galleryitemComponent)
                    let posNode = galleryNode.getChildByName("pos"+j)
                    galleryItem.position = posNode.position
                    galleryNode.addChild(galleryItem)
                }
            }
        }
        tianSeManager.instance.m_pOwner.setShowLoading(false)
        /*  */
        let num = len % 4
        let a = Math.floor(len * 0.25)
        if(num!=0)
        {   
            let designResolution: cc.Size = cc.Canvas.instance.designResolution
            let windths = designResolution.width
            this.scheduleOnce(()=>{
                this.setScrollViewOffset()
                this.listNode.getComponent(cc.Layout).enabled = false
                this.listNode.width = windths * a + windths * num * 0.25 //4
                if(this.listNode.width <  this.scrollView.node.width)
                {
                    this.listNode.width = this.scrollView.node.width
                }
            },0.3)
        }
        // AudioManager.playMusic(BUNDLE_NAME, `${MUSIC_PATH}${Music.bgm}`);
    }

    public setScrollViewOffset(){
        //   
        // tianSeManager.instance.propId = 1003  

        //TODD  
        // let unlockData =  paintDataManager.playUnlockSpineData
        // let len = unlockData.length
        // for (let j = 0; j < len; j++) {
        //     let element = unlockData[j];
        //     let galleryitemComponent =this.paintItemComponentMap.get(element.propId)
        //     galleryitemComponent.showUnlock()
        //     //
        //     paintDataManager.setENPaintItemPlayUnlockSpineById(element.propId)
        // }
        if(tianSeManager.instance.propId && tianSeManager.instance.propId != "0" && tianSeManager.instance.propId != "")
        {   
           this.setMoveOffset(tianSeManager.instance.propId)
        }else{
            // this.setMoveOffset(unlockData[len -1].propId)
        }
    }

    public setMoveOffset(propId){
        let designResolution: cc.Size = cc.Canvas.instance.designResolution
        let windths = designResolution.width
        console.log("" +propId)
        let galleryitemComponent =this.paintItemComponentMap.get(Number(propId))
        galleryitemComponent.node.getChildByName("num").color = cc.color(0,255,0)
        let pos = galleryitemComponent.node.convertToWorldSpaceAR(cc.v2(0, 0))
        let pos1 = this.scrollView.node.convertToNodeSpaceAR(pos)
        let p = cc.v2(pos1.x - windths / 2,pos1.y)
        this.scrollView.scrollToOffset(p, 0.5);
        // this.scheduleOnce(()=> galleryitemComponent.showUnlock(),0.5)
    }

    /*  */
    public refreshItem() {
        let ind = tianSeManager.instance.TanseIndex
        console.log(""+ind)
        let galleryitemComponent =this.paintItemComponentMap.get(ind)
        let data = paintDataManager.getPaintDataById(ind)
        galleryitemComponent.init(data);
        galleryitemComponent.node.active = false

        //
        let node = cc.instantiate(this.showItemPrefab)
        let tex = paintAssetsManager.instance.getPaintTextureById(ind)
        if(tex){
            let spr: cc.SpriteFrame = new cc.SpriteFrame(tex)
            node.getChildByName("paint").getComponent(cc.Sprite).spriteFrame = spr
        }else{
            let line = paintAssetsManager.instance.getPaintLineTextureById(ind)
            let spr1: cc.SpriteFrame = new cc.SpriteFrame(line)
            node.getChildByName("paint").getComponent(cc.Sprite).spriteFrame = spr1
        }
        this.node.addChild(node)
        let pos = galleryitemComponent.node.convertToWorldSpaceAR(cc.v2(0, 0))
        let pos1 = this.node.convertToNodeSpaceAR(pos)
        cc.tween(node)
        .to(0,{scale:3.5})
        .to(1.5,{scale:1,position:pos1})
        .call(()=>{
            galleryitemComponent.node.active = true
            node.destroy()
        })
        .start()
    }

    public onDestroy(){
        for(let i = 0; i < this.assetMap.length; i ++) {
            cc.assetManager.releaseAsset(this.assetMap[i]);
        }
        this.assetMap = [];
        EventSystem.off("callCocos_data",this.callCocos_data,this);
    }
}


