import { LIFE_CYCLE_PARAMS } from "../../kit/events/events";
import ResLoader from "../../kit/framework/load/ResLoader";
import AudioManager from "../../kit/system/audio/AudioManager";
import EventSystem from "../../kit/system/event/EventSystem";
import { BUNDLE_NAME, MESSAGE_IN_GALLERY, Music, MUSIC_PATH} from "./src/paintConfig";
import paintDataManager from "./src/paintDataManager";
import { tianSeManager } from "./src/tianSeManager";
import TigoManager from "./src/TigoManager";
const { ccclass, property } = cc._decorator;
@ccclass
export default class paint extends cc.Component {

    @property(cc.Node)
    content: cc.Node = null;

    @property(cc.Node)
    btnBack: cc.Node = null;

    @property(cc.Prefab)
    firstPaintGame: cc.Prefab = null;

    @property(cc.Prefab)
    paintGroup: cc.Prefab = null;

    public paintGroupNode: cc.Node = null
    public loadingNode: cc.Node = null
    public firstPaintGameNode: cc.Node = null

    public start() {
        this.setBackVisit(true)
    }

    public onLoad() {
        let userData = TigoManager.instance.getBaseInfo();
        if (userData) {
            let ud = JSON.parse(userData);
            TigoManager.instance.token = ud.token
            TigoManager.instance.setUserId(ud.userId);
            TigoManager.instance.setGameId(ud.gameId);
            TigoManager.instance.setPropId(ud.propId);
            tianSeManager.instance.setPropId(ud.propId)
            TigoManager.instance.setClassId(ud.lessonId);
            TigoManager.instance.setModuleId(ud.moduleId);
            TigoManager.instance.setLessonModuleId(ud.lessonModuleId);
            console.log("gameId == " + TigoManager.instance.classId + "  userId == " + TigoManager.instance.userId);
        }

        tianSeManager.instance.setOwner(this)
        this.regisEvent()
        this.initPaintData()
    }

    public initPaintData() {
        paintDataManager.init().then(() => {
            ResLoader.loadRes("prefab/loaDing", cc.Prefab, (err, res) => {
                this.loadingNode = cc.instantiate(res)
                this.loadingNode.active = false
                this.node.getChildByName("uiNode").addChild(this.loadingNode)

                let unLockData = paintDataManager.getUnlockPaintList()
                if(unLockData.length == 0)
                {
                    console.log("")
                }

                if (!cc.sys.isNative) {
                    var splash = document.getElementById('splash');
                    splash.style.display = 'none';
                }
                this.initUI()
            }, BUNDLE_NAME)
        },()=>{
            this.initUI()
        })
    }

    /*  */
    public openPaint() {
        this.firstPaintGameNode = cc.instantiate(this.firstPaintGame)
        this.content.addChild(this.firstPaintGameNode)
    }

    public setShowLoading(able) {
        this.loadingNode.active = able
    }

    /*  */
    public openPaintGroup() {
        if (cc.isValid(this.firstPaintGameNode)) {
            this.firstPaintGameNode.destroy()
        }
        this.paintGroupNode = cc.instantiate(this.paintGroup)
        this.content.addChild(this.paintGroupNode)
    }

    public initUI() {
        let openGallery = paintDataManager.getOpenGalleryState()
        if (!openGallery) {
            this.openPaint()
        } else {
            this.openPaintGroup()
        }
        console.log("--->",openGallery)
        AudioManager.playMusic(BUNDLE_NAME, `${MUSIC_PATH}${Music.bgm}`)
    }

    public setBackVisit(able?: boolean) {
        if (cc.sys.isNative) {
            this.btnBack.active = false
        } else {
            this.btnBack.active = able
        }
    }

    public Back(event, choose) {
        console.log("========")
        event.target.active = false
        AudioManager.stopEffect()
        AudioManager.stopMusic()
        cc.audioEngine.pauseMusic();
        cc.audioEngine.pauseAllEffects();
        cc.game.pause();
        TigoManager.instance.callAppToClose("replayOrNext", true);
    }

    public regisEvent() {
        // cc.game.on(cc.game.EVENT_HIDE, function () {
        //     console.log("~~~~~~~~");
        //     cc.audioEngine.pauseMusic();
        //     cc.audioEngine.pauseAllEffects();
        //     cc.game.pause();
        // });

        // cc.game.on(cc.game.EVENT_SHOW, function () {
        //     console.log("~~~~~~~~");
        //     cc.audioEngine.resumeMusic();
        //     cc.audioEngine.resumeAllEffects();
        //     cc.game.resume();
        // });

        EventSystem.on(MESSAGE_IN_GALLERY, this.openPaintGroup, this);
        EventSystem.on(LIFE_CYCLE_PARAMS, this.setPropId, this);
    }

    public setPropId(_data) {
        let data = _data.data
        console.log("========>",_data)
        tianSeManager.instance.setPropId(data.propId)
    }

    public onDestroy() {
        EventSystem.off(MESSAGE_IN_GALLERY, this.openPaintGroup, this);
        EventSystem.off(LIFE_CYCLE_PARAMS, this.setPropId, this);
    }
}




