import EventSystem from "../../../../kit/system/event/EventSystem";
import { paintAssetsManager } from "../paintAssetsManager";
import { MESSAGE_IN_GALLERY } from "../paintConfig";
import { tianSeManager } from "../tianSeManager";

const { ccclass, property } = cc._decorator;
@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Node)
    scaleNode: cc.Node = null

    @property(cc.Node)
    guideNode: cc.Node = null

    @property(cc.Node)
    showNode: cc.Node = null

    isTouch: boolean = false

    left_pos = cc.v3(0, 0)
    right_pos = cc.v3(0, 0)

    left_light: cc.Node = null
    right_light: cc.Node = null
    onLoad() {
        this.recordLightPos()
        this.node.on(cc.Node.EventType.TOUCH_START, this.touchStart, this)
    }

    onEnable() {
        this.scaleNode.scale = 1.5
        this.guideNode.active = false
        this.showNode.active = false
        this.scheduleOnce(() => {
            this.guideNode.active = true
        }, 4)
    }

    recordLightPos() {
        this.left_light = this.node.getChildByName("left_light")
        this.right_light = this.node.getChildByName("right_light")
        this.left_pos = this.left_light.position
        this.right_pos = this.right_light.position
    }

    setScaleNode() {
        let left_node = this.node.getChildByName("left_node")
        let right_node = this.node.getChildByName("right_node")
        this.left_light.position = this.left_pos
        this.right_light.position = this.right_pos

        cc.tween(this.left_light).to(1.5, { position: left_node.position }).start()
        cc.tween(this.right_light).to(1.5, { position: right_node.position }).start()
        cc.tween(this.scaleNode).to(1.5, { scale: 1 }).call(() => this.isTouch = true).start()
    }

    touchStart() {
        if (this.isTouch) {
            this.unscheduleAllCallbacks()
            this.guideNode.active = false
            this.isTouch = false
            this.node.active = false
            if (tianSeManager.instance.curIsGallery) {
                tianSeManager.instance.closePaint()
            } else {      //
                if (tianSeManager.instance.unLockCount >= tianSeManager.instance.max_tanse_index) {
                    EventSystem.emit(MESSAGE_IN_GALLERY);
                }
            }
        }
    }

    setShowImage(tanIndex) {
        this.scheduleOnce(()=>{
            let _tex = null
            let tex = paintAssetsManager.instance.getPaintTextureById(tanIndex)
            let tex_line = paintAssetsManager.instance.getPaintLineTextureById(tanIndex)
            if (tex) {
                console.log(" ======")
                _tex = tex
            } else {
                console.log(" ======")
                _tex = tex_line
            }
            let spr: cc.SpriteFrame = new cc.SpriteFrame()
            this.showNode.active = true
            spr.setTexture(_tex)
            this.showNode.getComponent(cc.Sprite).spriteFrame = spr;
            this.setScaleNode()
        })
    }

    onDestroy() {
        this.node.off(cc.Node.EventType.TOUCH_START, this.touchStart, this)
    }
}
