import tianSeNode from "./tianSeNode";
import { GUIDE_STATE } from "../paintConfig";
import pen from "../pen";
import showPaint from "./showPaint";
import { tianSeManager } from "../tianSeManager";
import paintDataManager from "../paintDataManager";
const {ccclass, property} = cc._decorator;
@ccclass
export default class tanseMain extends cc.Component {

    @property(cc.Prefab)
    penPre: cc.Prefab = null;

    @property(cc.Prefab)
    guidePre: cc.Prefab = null;

    @property(cc.Prefab)
    tansePre: cc.Prefab = null;

    @property(cc.Prefab)
    showPaintPre: cc.Prefab = null;

    public isGuide = true
    public penNode: cc.Node = null
    public guideNode: cc.Node = null
    public tanseNode: cc.Node = null

    public onLoad () {
        tianSeManager.instance.init(this,true,true)
        this.loadPropDetail()
    }
    
    public loadPropDetail(){
        paintDataManager.getSeverPropDetails(tianSeManager.instance.TanseIndex).then(()=>{
            this.tanseNode = cc.instantiate(this.tansePre)
            this.node.addChild(this.tanseNode)

            this.guideNode = cc.instantiate(this.guidePre)
            this.node.addChild(this.guideNode)
            this.guideNode.active = false

            this.setGuide()
        });
    }

    public start () {
       
    }

    public nextPaint(){
        this.loadCurPropDetail()
    }

    async loadCurPropDetail(){
        paintDataManager.getSeverPropDetails(tianSeManager.instance.TanseIndex).then(()=>{
            this.tanseNode.getComponent<tianSeNode>(tianSeNode).initPaint()
        });
    }

    /*  */
    public setGuide() {
        this.guideNode.active = false
        this.unscheduleAllCallbacks()
        if(!this.isGuide || tianSeManager.instance.guideState == GUIDE_STATE.DONE) return
        switch (tianSeManager.instance.guideState) {
            case GUIDE_STATE.INIT:
                this.scheduleOnce(() => {
                        this.guideNode.active = true
                        this.guideNode.x = this.tanseNode.getChildByName("penNode").x + 100
                }, 2)
                break
            case GUIDE_STATE.CLICK_HB:
                this.scheduleOnce(() => {
                        this.guideNode.active = true
                        this.guideNode.x = this.tanseNode.x - 100
                }, 2)
                break
            case GUIDE_STATE.CLICK_FILL:
                tianSeManager.instance.guideState = GUIDE_STATE.DONE
                this.tanseNode.getChildByName("done").active = true
                break
            case  GUIDE_STATE.DONE:
                this.tanseNode.getChildByName("done").active = false
                break
        }
    }
}
