import { BUNDLE_NAME,GUIDE_STATE,LOCAL_PROP_DETAIL,MUSIC_PATH } from "./paintConfig";
import { tianSeManager } from "./tianSeManager";
import AudioManager from "../../../kit/system/audio/AudioManager";
import paintDataManager from "./paintDataManager";
const {ccclass, property} = cc._decorator;
@ccclass
export default class pen extends cc.Component {

    @property(cc.Node)
    penList: cc.Node[] = [];

    public basicX:number = 80
    public color_config:any = null
    
    public onEnable(){
        // this.refreshColorNode()
    }

    /*  */
    public refreshColorNode(){
        let len = this.penList.length
        let tanse_index = tianSeManager.instance.TanseIndex
        let color_index = tianSeManager.instance.ColorIndex
        console.log("tanse_index =================================================>"+tanse_index)
        console.log("ColorIndex =================================================>"+color_index)
        let data = paintDataManager.getPaintPropDetailById(tanse_index)
        this.color_config = data.itemData
        if(!this.color_config)
        {   
            console.log("pen ====> ")
            this.color_config = LOCAL_PROP_DETAIL[0].itemData
        }
        console.log("this.color_config =================================================>",this.color_config)
        // this.color_config = LOCAL_PROP_DETAIL[0].itemData
        for (let i = 0; i < len; i++) {
            let able = i == color_index
            const node = this.penList[i];

            let floor = node.getChildByName("floor")
            let select = node.getChildByName("select")
            let unselect = node.getChildByName("unselect")
            let _color =  this.color_config[i].colorValue 
            floor.color = new cc.Color().fromHEX("#" +_color)
        
            select.active = able
            unselect.active = !able
            node.x = able == true ? 37 : this.basicX

            if(able) {
                tianSeManager.instance.CurColor = floor.color
            }
        }
    }

    /*  */
    public clickEventBack (event,choose) {
        if(choose == tianSeManager.instance.ColorIndex){
            return
        }
        tianSeManager.instance.ColorIndex = Number(choose)
        this.refreshColorNode()
        if(tianSeManager.instance.guideState == GUIDE_STATE.INIT)
        {
            tianSeManager.instance.guideState = GUIDE_STATE.CLICK_HB
            tianSeManager.instance.setGuide()
        }
        // if(cc.sys.isNative)
        // {   
            let effect = this.color_config[choose].colorName
            AudioManager.playEffect(BUNDLE_NAME, `${MUSIC_PATH}${effect}`);
        // }
    }
}
