import AudioManager from "../../../../kit/system/audio/AudioManager";
import ResLoader from "../../../../kit/framework/load/ResLoader";
import { BUNDLE_NAME, GUIDE_STATE, Music, MUSIC_PATH, SAVE_PAINT_SIGN } from "../paintConfig";
import { LocalStorageUtils } from "../../../../kit/utils/LocalStorageUtils";
import paintDataManager from "../paintDataManager";
import { paintAssetsManager } from "../paintAssetsManager";
import { storageParams } from "../paintInterface";
import TigoManager from "../TigoManager";
import { tianSeManager } from "../tianSeManager";
import showPaint from "./showPaint";
import pen from "../pen";

const { ccclass, property } = cc._decorator;
@ccclass
export default class tianSeNode extends cc.Component {

    @property(cc.Camera)
    paint_Camera: cc.Camera = null;

    @property(cc.Node)
    paint1: cc.Node = null;

    @property(cc.Node)
    paint2: cc.Node = null;

    @property(cc.Node)
    doneBtn: cc.Node = null;

    @property(cc.Node)
    topMask: cc.Node = null;
    isFirstSave:boolean = false

    @property(cc.Prefab)
    showPaintPre: cc.Prefab = null;

    @property(cc.Prefab)
    penPre: cc.Prefab = null;

    private tetTexture: cc.RenderTexture = null;
    private curDrawTexture2D: cc.Texture2D = null;

    /**
     *  RGBA    x  x 4RGBAUint8Array 
     */
    private winWidth: number = 0;
    private winHeight: number = 0;

    private curTextureData: any = {};
    private savedTexutreData: any = {};

    private b_inited: boolean = false;

    /**
     * 0 -1
     */
    private curDrawMode = 0;
    private checkedPointDic: any = {};
    private checkPointList: Array<any> = [];
    public paintFrame = []

    public penNode: cc.Node = null
    public showPaintNode: cc.Node = null

    public onLoad() {
        this.addEvent()

        this.penNode = cc.instantiate(this.penPre)
        this.node.addChild(this.penNode)

        this.initPaint()
        // AudioManager.playMusic(BUNDLE_NAME, `${MUSIC_PATH}${Music.bgm}`);
    }
    
    public initPaint(){
        this.b_inited = false;  
        this.node.getChildByName("done").active = true
        this.penNode.getComponent<pen>(pen).refreshColorNode()
        this.loadPaintFrame()
    }

    public loadPaintFrame(){
        let tex = paintAssetsManager.instance.getPaintLineTextureById(tianSeManager.instance.TanseIndex)
        if(tex){
            console.log(""+tianSeManager.instance.TanseIndex)
            let spr: cc.SpriteFrame = new cc.SpriteFrame(tex)
            this.paint1.getComponent(cc.Sprite).spriteFrame = spr
            this.initView()
        }else{
            let data = paintDataManager.getPaintDataById(tianSeManager.instance.TanseIndex)
            ResLoader.loadRes(data.iconUrl,cc.Texture2D,(err,res)=>{
                if(err)
                {
                    console.log("ID=====>",tianSeManager.instance.TanseIndex)
                }else{
                    console.log("",res)
                    let spr: cc.SpriteFrame = new cc.SpriteFrame(res)
                    this.paint1.getComponent(cc.Sprite).spriteFrame = spr;
                    paintAssetsManager.instance.setPaintLineTextureById(tianSeManager.instance.TanseIndex,res)
                }
                this.initView()
            },BUNDLE_NAME)
        }
    }

    protected initView(): void {
        this.curDrawMode = 0;
        this.topMask.active = false
        this.paint_Camera.ortho = true
        this.paint_Camera.alignWithScreen = false;
        this.tetTexture = new cc.RenderTexture();
        this.curDrawTexture2D = new cc.Texture2D();
        this.scheduleOnce(()=>{this.resetRendData()})
    }

    public resetRendData() {
        this.paint2.active = false
       
        this.paint_Camera.orthoSize = this.paint1.height * .5;

        this.winWidth = this.paint1.width
        this.winHeight = this.paint1.height

        this.paint2.x = this.paint1.x
        this.paint2.y = this.paint1.y
        this.paint_Camera.node.x = this.paint1.x
        this.paint_Camera.node.y = this.paint1.y

        
        this.paint2.setContentSize(this.paint1.getContentSize())
        this.paint_Camera.node.setContentSize(this.paint1.getContentSize())

       

        // @ts-ignore
        this.tetTexture._depthStencilBuffer = null;
        // @ts-ignore
        this.tetTexture.initWithSize(this.paint1.width, this.paint1.height, cc.gfx.RB_FMT_S8);
        this.paint_Camera.targetTexture = this.tetTexture;
        this.paint_Camera.render(this.paint1)

        this.scheduleOnce(() => {
            let textureData: any = this.tetTexture.readPixels();
            this.savedTexutreData = textureData.slice(); // slice()
            this.curTextureData = textureData.slice();

            // this.tetTexture.setFlipY(false);
            this.b_inited = true;
            let spr: cc.SpriteFrame = new cc.SpriteFrame()
            this.curDrawTexture2D.initWithData(this.filpYImage(this.curTextureData, this.winWidth, this.winHeight), cc.Texture2D.PixelFormat.RGBA8888, this.winWidth, this.winHeight);
            spr.setTexture(this.curDrawTexture2D);
            this.paint2.getComponent(cc.Sprite).spriteFrame = spr;
            this.paint2.active = true
        }, 1)
    }

    /**
     * RGBA
     * @param data 
     * @param width 
     * @param height 
     */
    private filpYImage(data, width, height): any {
        // create the data array
        let picData = new Uint8Array(width * height * 4);
        let rowBytes = width * 4;
        for (let row = 0; row < height; row++) {
            let srow = height - 1 - row;
            let start = srow * width * 4;
            let reStart = row * width * 4;
            // save the piexls data
            for (let i = 0; i < rowBytes; i++) {
                picData[reStart + i] = data[start + i];
            }
        }
        return picData;
    }

    /**
     * 
     */
    private addEvent(): void {
        this.paint1.on(cc.Node.EventType.TOUCH_END, (event: cc.Event.EventTouch) => {
            if (!this.b_inited || !this.curTextureData ) return;
           
            if(tianSeManager.instance.guideState == GUIDE_STATE.CLICK_HB)
            {
                tianSeManager.instance.guideState = GUIDE_STATE.CLICK_FILL
                tianSeManager.instance.setGuide()
            }
            let tempPoint = event.getLocation()

            let pos = this.paint1.convertToNodeSpaceAR(cc.v2(tempPoint.x, tempPoint.y))
            pos.x = pos.x + this.paint1.width * .5
            pos.y = pos.y + this.paint1.height * .5
            this.checkColorFill(Math.floor(pos.x), Math.floor(pos.y));

            let spr: cc.SpriteFrame = new cc.SpriteFrame()
            this.curDrawTexture2D.initWithData(this.filpYImage(this.curTextureData, this.winWidth, this.winHeight), cc.Texture2D.PixelFormat.RGBA8888, this.winWidth, this.winHeight);
            spr.setTexture(this.curDrawTexture2D);
            // this.curDrawTexture2D.setFilters(cc.Texture2D.Filter.NEAREST,cc.Texture2D.Filter.NEAREST)
            this.paint2.getComponent(cc.Sprite).spriteFrame = spr;
        }, this);
    }

    //
    private checkColorFill(x, y): void {
        if (!this.inArea(x, y)) return;
        this.checkPointList = [];
        this.checkedPointDic = {};

        this.checkPushPoint(this.checkedPointDic, { x: x, y: y });
        let _checkPoint = this.checkPointList[0]; // 
        let _curIndex = Math.floor(this.winWidth * _checkPoint.y * 4 + _checkPoint.x * 4);
        let _b_IsWhite1 = this.savedTexutreData[_curIndex] + this.savedTexutreData[_curIndex + 1] + this.savedTexutreData[_curIndex + 2];
        if (_b_IsWhite1) {
            AudioManager.playEffect(BUNDLE_NAME, `${MUSIC_PATH}${Music.clickFill}`);
        }
        while (this.checkPointList.length > 0) {
            let checkPoint = this.checkPointList.pop(); // 

            if (!checkPoint) continue;
            if (!this.inArea(checkPoint.x, checkPoint.y)) continue;

            let curIndex = Math.floor(this.winWidth * checkPoint.y * 4 + checkPoint.x * 4);
            let colorTotal = this.savedTexutreData[curIndex] + this.savedTexutreData[curIndex + 1] + this.savedTexutreData[curIndex + 2];
            let opi = this.savedTexutreData[curIndex + 3]
            let b_IsWhite = colorTotal > 300; // 300 255*3
            if (!b_IsWhite) continue; // 

            // this.checkedPointDic[checkPoint.x + "_" + checkPoint.y] = true;
            switch (this.curDrawMode) {
                case 0:
                    this.curTextureData[curIndex] = tianSeManager.instance.CurColor.r;
                    this.curTextureData[curIndex + 1] = tianSeManager.instance.CurColor.g;
                    this.curTextureData[curIndex + 2] = tianSeManager.instance.CurColor.b;
                    break;
                case -1:
                    this.curTextureData[curIndex] = 255;
                    this.curTextureData[curIndex + 1] = 255;
                    this.curTextureData[curIndex + 2] = 255;
                    break;
                default:
                    break;
            }
            this.checkPushPoint(this.checkedPointDic, { x: checkPoint.x, y: checkPoint.y + 1 });
            this.checkPushPoint(this.checkedPointDic, { x: checkPoint.x, y: checkPoint.y - 1 });
            this.checkPushPoint(this.checkedPointDic, { x: checkPoint.x + 1, y: checkPoint.y });
            this.checkPushPoint(this.checkedPointDic, { x: checkPoint.x - 1, y: checkPoint.y });
        }
    }

    // var a = {}
    // a[10] = {}
    // a[10][20] = true
    // console.log(a) = {10{}}
    /** 
     * 
     * @param checkedPointDic 
     * @param checkPoint 
     */
    private checkPushPoint(checkedPointDic, checkPoint) {
        if (checkedPointDic[checkPoint.x] && checkedPointDic[checkPoint.x][checkPoint.y]) return;

        if (!checkedPointDic[checkPoint.x]) checkedPointDic[checkPoint.x] = {};
        checkedPointDic[checkPoint.x][checkPoint.y] = true;
        this.checkPointList.push(checkPoint);
    }

    /**
     *               
     * @param x 
     * @param y 
     */
    private inArea(x: number, y: number): boolean {
        return x >= 0 && x <= this.winWidth && y >= 0 && y <= this.winHeight;
    }

    public doneCurTs(event, choose) {
        event.target.active = false
        paintDataManager.setPaintItemUnlockById(tianSeManager.instance.TanseIndex)
        paintAssetsManager.instance.setPaintTextureById(tianSeManager.instance.TanseIndex,this.curDrawTexture2D)

        if(this.showPaintNode)
        {   
            this.showPaintNode.active = true
            this.showPaintNode.getComponent<showPaint>(showPaint).setShowImage(tianSeManager.instance.TanseIndex)
        }else{
            this.showPaintNode = cc.instantiate(this.showPaintPre)
            if(this.showPaintNode){
                this.node.addChild(this.showPaintNode)
                this.showPaintNode.getComponent<showPaint>(showPaint).setShowImage(tianSeManager.instance.TanseIndex)
            }
        }
        this.saveFileToLocal(this.curTextureData)

        if(!this.isFirstSave)
        {   
            let platform = paintDataManager.getPlatform()
            if(platform == "cn")
            {
                LocalStorageUtils.setBool(SAVE_PAINT_SIGN,true)
            }else{
                let params:storageParams= {
                    method:"PUT",
                    key:SAVE_PAINT_SIGN,
                    value:"1",
                }
                console.log("H5 ")
                TigoManager.instance.callAppToStorage(params) || {}
            }
            this.isFirstSave = true
            tianSeManager.instance.guideState = GUIDE_STATE.DONE
            tianSeManager.instance.setGuide()
        }
        AudioManager.playEffect(BUNDLE_NAME, `${MUSIC_PATH}${Music.done}`);
    }

    public saveFileToLocal(data){
        if (cc.sys.isNative) {
            console.log(" isNative ")
            let path = paintDataManager.getNativePathByIndex(tianSeManager.instance.TanseIndex)
            let filePath = jsb.fileUtils.getWritablePath() + path;
            let _data = this.filpYImage(data, this.winWidth, this.winHeight)
            // @ts-ignore
            let success = jsb.saveImageData(_data, this.winWidth, this.winHeight, filePath)
            if (success) {
                // cc.assetManager.loadRemote(filePath,(err,res:any)=>{
                //     if(err)
                //     {
                //         console.log("",err)
                //     }
                //     paintAssetsManager.instance.setPaintTextureById(tianSeManager.instance.TanseIndex,res)
                //    
                // })
                tianSeManager.instance.change()
                this.resetTianSeData()
                console.log("save image data success, file: " + filePath);
            }
            else {
                cc.error("save image data failed!");
            }
        }else {
            console.log(" isMobile ")
            this.saveImageToWeb()
        }
    }

    public saveImageToWeb(){
        let canvas = document.createElement('canvas');
        let width = canvas.width = this.winWidth;
        let height = canvas.height = this.winHeight;
        let ctx = canvas.getContext('2d');

        let rowBytes = width * 4;
        for (let row = 0; row < height; row++) {
            let srow = height - 1 - row;
            let imageData = ctx.createImageData(width, 1);
            let start = srow * width * 4;
            for (let i = 0; i < rowBytes; i++) {
                imageData.data[i] = this.curTextureData[start + i];
            }
            ctx.putImageData(imageData, 0, row);
        }
        let dataURL = canvas.toDataURL();
        let params:storageParams= {
            method:"PUT",
            key:"paint_" + tianSeManager.instance.TanseIndex,
            type:"file",
            value:dataURL
        }
        TigoManager.instance.callAppToStorage(params)
        tianSeManager.instance.change()      // tianSeManager.instance.TanseIndex++  
        this.resetTianSeData()
    }     

    public resetTianSeData(){
        this.savedTexutreData = {}
        this.curTextureData = {}
        this.checkedPointDic= {};
        this.checkPointList= [];
    }
    public onDestroy() {


    }
}


