import { GUIDE_STATE, MESSAGE_IN_GALLERY } from "./paintConfig";
import paintDataManager from "./paintDataManager";

const { ccclass, property } = cc._decorator;
@ccclass
export class tianSeManager {
    private static _instance: tianSeManager

    public propId: any = null
    public guideState: string = ""
    public curIsGallery: boolean = false
    public MainNode: any = null;
    public unLockCount: number = 0;
    public m_pOwner: any = null;
    private tanse_index: number = null;
    private color_index: number = null;
    public max_tanse_index: number = 0;
    public open_gallery_in_firstPaint: Boolean = false
    private cur_Color: cc.Color = cc.color(255, 255, 255);

    /*  */
    public init(main: cc.Component,isGuide = false,firstGame = false): void {
        this.MainNode = main
        this.color_index = -1

        if(firstGame){
            let unLockData = paintDataManager.getUnlockPaintList()
            if(unLockData)
            {
                this.tanse_index = unLockData[this.unLockCount].propId
                this.max_tanse_index = unLockData.length
            }
        }else{
            this.curIsGallery = true
            this.unLockCount = 0
            this.tanse_index = 0
            this.max_tanse_index = 0
        }

        if (isGuide) {
            this.guideState = GUIDE_STATE.INIT
        }
    }

    public change(): void {
        this.unLockCount++
        if (this.unLockCount >= this.max_tanse_index) {
            this.open_gallery_in_firstPaint = true     //
            // EventSystem.emit(MESSAGE_IN_GALLERY);
            return
        }
        let unLockData = paintDataManager.getUnlockPaintList()
        if(unLockData){
            tianSeManager.instance.TanseIndex = unLockData[this.unLockCount].propId
            this.MainNode.nextPaint()
        }
    }

    public setPropId(id){
        let able = paintDataManager.getOpenGalleryState()
        if(able)
        {
            this.propId = id
        }
    }

    public setOwner(owner){
        this.m_pOwner = owner
    }
    
    public openPaint(data) {
        this.MainNode.openPaint(data)
    }

    public closePaint() {
        this.MainNode.closePaint()
    }

    public refreshItem(){
        this.MainNode.refreshItem()
    }

    public showQuitAlertPre() {
        this.MainNode.showQuitAlertPre()
    }

    public setGuide() {
        this.MainNode.setGuide()
    }

    public get TanseIndex(): number {
        return this.tanse_index;
    }

    public get ColorIndex(): number {
        return this.color_index;
    }

    public set ColorIndex(value: number) {
        this.color_index = value;
        // console.log("" + this.color_index)
    }

    public get CurColor(): cc.Color {
        return this.cur_Color;
    }

    public set CurColor(value: cc.Color) {
        this.cur_Color = value;
        // console.log("", this.cur_Color)
    }

    public set TanseIndex(value: number) {
        this.tanse_index = value;
    }

    public static get instance(): tianSeManager {
        if (!tianSeManager._instance) {
            tianSeManager._instance = new tianSeManager();
        }
        return tianSeManager._instance;
    }
}
