import { SET_TOP_BAR_ACTIVE } from "../../../../kit/events/events";
import EventSystem from "../../../../kit/system/event/EventSystem";
import { tianSeManager } from "../tianSeManager";
import Gallery from "./Gallery";
import tianSeNodeInGallery from "./tianSeNodeInGallery";
const { ccclass, property } = cc._decorator;
@ccclass
export default class paintMain extends cc.Component {

    @property(cc.Node)
    contentNode: cc.Node = null;

    @property(cc.Prefab)
    galleryPre: cc.Prefab = null;

    @property(cc.Prefab)
    tiansePre: cc.Prefab = null;

    @property(cc.Prefab)
    quitAlertPre: cc.Prefab = null;

    public quitAlertNode: cc.Node = null
    public galleryNode: cc.Node = null
    public tianseNode: cc.Node = null

    onLoad() {
        tianSeManager.instance.init(this)
        this.galleryNode = cc.instantiate(this.galleryPre)
        this.node.addChild(this.galleryNode)
        this.galleryNode.active = true
    }

    start() {
        this.quitAlertNode = cc.instantiate(this.quitAlertPre)
        this.node.addChild(this.quitAlertNode)
        this.quitAlertNode.active = false
        this.quitAlertNode.zIndex = cc.macro.MAX_ZINDEX
    }

    openPaint(data) {
        EventSystem.emit(SET_TOP_BAR_ACTIVE,{active:false});
        tianSeManager.instance.m_pOwner.setBackVisit(false)
        if(!this.tianseNode){
            this.tianseNode = cc.instantiate(this.tiansePre)
            this.node.addChild(this.tianseNode)
        }
        this.tianseNode.active = true
        this.tianseNode.getComponent<tianSeNodeInGallery>(tianSeNodeInGallery).initPaint(data)
    }

    closePaint() {
        this.refreshItem()
        this.tianseNode.active = false
        tianSeManager.instance.m_pOwner.setBackVisit(true)
        EventSystem.emit(SET_TOP_BAR_ACTIVE,{active:true});
    }

    refreshItem() {
        this.galleryNode.getComponent<Gallery>(Gallery).refreshItem()
    }

    showQuitAlertPre() {
        this.quitAlertNode.active = true
    }
    // update (dt) {}
}
