
export class paintAssetsManager{
    private paintTextureMap:Map<number,cc.Texture2D> = new Map();         //
    private paintLineTextureMap:Map<number,cc.Texture2D> = new Map();     //
    private static _instance: paintAssetsManager;

    public setPaintTextureById(id,texture){
        this.paintTextureMap.set(id,texture)
        console.log("ID---->",id)
    }

    public getPaintTextureById(id):cc.Texture2D{
        if(this.paintTextureMap.get(id)){
            console.log("ID---->",id)
        }
        return this.paintTextureMap.get(id)
    }

    public setPaintLineTextureById(id,texture){
        this.paintLineTextureMap.set(id,texture)
        console.log("ID---->",id)
    }

    public getPaintLineTextureById(id):cc.Texture2D{
        if(this.paintLineTextureMap.get(id)){
            console.log("ID---->",id)
        }
        return this.paintLineTextureMap.get(id)
    }

    public getPaintTextureData(){
        return this.paintTextureMap
    }

    public static get instance(): paintAssetsManager {
        if (!paintAssetsManager._instance) {
            paintAssetsManager._instance = new paintAssetsManager();
        }
        return paintAssetsManager._instance;
    }
}