        let url = TEST_SEVER_URL + SEVER_INTERFACE.proplist
        // TigoManager.instance.token = "eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiJcdTAwMDcrODYxODM0NTE3OTgwMyIsImNyZWF0ZWQiOjE2MzIzOTYxODY3NDEsImV4cCI6MTY0Nzk0ODE4Nn0.ftUZ7vG9fvA3bvifoii8OYWI-WRIn1J__e3Mt7BidpOQ8ajTLZJwSOqDo--U4mfN0NL0MXdpXGUkVsyweCM1QA"
        // params = {
        //     userId:"265",
        //     gameId:16
        // }
        // let url1 = TEST_SEVER_URL + SEVER_INTERFACE.prop + 34
        // HttpSystem.instance.httpGet(url1,null,`{"Authorization": "Bearer ${TigoManager.instance.token}"}`).then((res) => {
        //     console.log("",res)
        //     // let data = JSON.parse(res.data.expandDes)
