import ResLoader from "../../../../kit/framework/load/ResLoader";
import { BUNDLE_NAME, SEVER_INTERFACE, TEST_SEVER_URL } from "../paintConfig";
import { paintAssetsManager } from "../paintAssetsManager";
import { tianSeManager } from "../tianSeManager";
import paintDataManager from "../paintDataManager";

const { ccclass, property } = cc._decorator;
@ccclass
export default class galleryitem extends cc.Component {

    @property(cc.Node)
    lock: cc.Node = null;

    @property(cc.Node)
    light: cc.Node = null;

    @property(cc.Node)
    mubu: cc.Node = null;

    @property(cc.Node)
    paint: cc.Node = null;

    itemInfo: any = {}
    isClick = false

    public onLoad() {

    }

    public init(data) {
        this.itemInfo = data
        this.isClick = false
        this.node.getChildByName("num").active = false
        this.node.getChildByName("num").getComponent(cc.Label).string = data.propId
        if(data.isSave)
        {   
            let tex = paintAssetsManager.instance.getPaintTextureById(data.propId)
            if(tex){
                console.log(""+data.propId)
                let spr: cc.SpriteFrame = new cc.SpriteFrame()
                spr.setTexture(tex);
                this.paint.getComponent(cc.Sprite).spriteFrame = spr;
                this.isClick = true
            }else{
                console.log("",tex)
               this.loadDefaultTexture()
            }
        }else
        {   
            console.log(""+data.propId)
            this.loadDefaultTexture()
        }
       
        this.light.active = data.isSave
        this.mubu.active = data.lockVal == 1
        this.lock.active =  data.lockVal == 1
    }

    public clickEvent(event, choose) {
        if (this.itemInfo.lockVal == 1 || !this.isClick) return
        tianSeManager.instance.TanseIndex = this.itemInfo.propId
        tianSeManager.instance.ColorIndex = -1
        // this.paint.getComponent(cc.Sprite).spriteFrame = null
        this.loadPropDetail()
    }

    async loadPropDetail(){
        let result = await paintDataManager.getSeverPropDetails(this.itemInfo.propId);
        tianSeManager.instance.openPaint(this.itemInfo)
    }

    public loadDefaultTexture(){
        let tex_line = paintAssetsManager.instance.getPaintLineTextureById(this.itemInfo.propId)
        if(tex_line)
        {   
            let spr:cc.SpriteFrame = new cc.SpriteFrame()
            spr.setTexture(tex_line);
            this.paint.getComponent(cc.Sprite).spriteFrame = spr;
            this.isClick = true
        }else{
            ResLoader.loadRes(this.itemInfo.iconUrl,cc.Texture2D,(err,res)=>{
                if(err)
                {
                    console.log("",err)
                }
                console.log(""+this.itemInfo.propId)
                let spr: cc.SpriteFrame = new cc.SpriteFrame()
                spr.setTexture(res);
                this.paint.getComponent(cc.Sprite).spriteFrame = spr;
                this.isClick = true
                paintAssetsManager.instance.setPaintLineTextureById(this.itemInfo.propId,res)
            },BUNDLE_NAME)
        }
        
    }

    showUnlock(){
        this.node.getChildByName("num").active = true
        this.node.getChildByName("num").getComponent(cc.Label).string = ""
        // this.scheduleOnce(()=>{
        //     this.node.getChildByName("num").active = false
        // },0.5)
    }
}
