import { SET_TOP_BAR_ACTIVE } from "../../../kit/events/events";
import EventSystem from "../../../kit/system/event/EventSystem";
import { tianSeManager } from "../src/tianSeManager";
const { ccclass, property } = cc._decorator;
@ccclass
export default class QuitLessonAlert extends cc.Component {

    @property(cc.Label)
    public label: cc.Label = null;


    public onLoad() {

    }

    public init(data?: any): void {

    }

    public onClickClose(): void {
        this.node.active = false
        EventSystem.emit(SET_TOP_BAR_ACTIVE,{active:true});
        tianSeManager.instance.MainNode.tianseNode.active = false
        tianSeManager.instance.MainNode.refreshItem()
    }

    public onClickOk(): void {
        this.node.active = false
    }
}
